.saved-news-title-block__title {
    margin-left: 104px;
    font-family: 'roboto', Helvetica, Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.33px;
    color: rgba(26, 27, 34, 0.5);
}

@media screen and (max-width: 1050px) {
    .saved-news-title-block__title {
        margin-left: 40px;
        font-size: 14px;
    }
}

@media screen and (max-width: 570px) {
    .saved-news-title-block__title {
        margin-left: 16px;
        font-size: 12px;
    }
}
